<template>
    <div class="user_exchange"
        v-loading="pageLoading"
        element-loading-text="加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
    >
        <div>
            <div class="section_head">
                <span class="sp_m">
                    总资产：<span class="money">￥{{ getTotalMoney() }}</span>
                </span>
                <!-- <a href="javascript:;" @click="reload">
                    <i class="el-icon-refresh"></i>
                </a> -->
                <div class="clearfix"></div>
            </div>

            <div class="section_content" 
            >
                <el-row :gutter="10">
                    <el-col :span="6">
                        <el-card shadow="hover">
                            <div class="top">
                                <div>
                                    钱包余额 <span class="money">￥{{ getMoney(0) }}</span>
                                </div>
                                <div class="clearfix"></div>
                            </div>
                            <div class="bottom">
                                <div>
                                    <el-button type="text" icon="fas fa-undo" :loading="_getLoading(0)" @click="openForm(0, 3)">一键全部回收</el-button>
                                </div>
                            </div>
                        </el-card> 
                    </el-col>
                    <el-col :span="6" :key="product.id" v-for="product in filterProduct">
                        <el-card shadow="hover" :class="product.is_fix == 'Y' ? 'fixed' : ''">
                            <div class="top">
                                <div>
                                    <img v-if="product.icon" :src="product.icon | imgFormatter" style="max-width: 40px;" height="40" />
                                </div>
                                <div>
                                    {{ product.name }} <span class="money">￥{{ getMoney(product.id) }}</span>
                                    <a href="javascript:;" v-if="product.is_fix == 'N'" class="product_reload" @click="productReload(product.id)">
                                        <i class="el-icon-refresh"></i>
                                    </a>
                                </div>
                                <div class="clearfix"></div>
                            </div>
                            <div class="bottom">
                                <div v-if="product.is_fix == 'N'">
                                    <el-button type="text" @click="openForm(product.id, 2)">转入</el-button>
                                    <el-button type="text" @click="openForm(product.id, 1)">转出</el-button>
                                    <el-button type="text" :loading="_getLoading(product.id)" @click="openForm(product.id, 3)">回收</el-button>
                                </div>
                                <div v-else>
                                    <el-button type="text">接口维护中</el-button>
                                </div>
                            </div>
                        </el-card>
                    </el-col>
                </el-row>
            </div>
        </div>

        <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            width="400px"
        >
            <p><span v-html="fromText"></span><i class="fas fa-long-arrow-alt-right" style="margin: 0 14px;"></i> <span v-html="toText"></span></p>
            <el-form ref="form" class="trans_form" :model="formData" :rules="formRules" label-position="top" label-width="80px">
                <span v-if="getMoney(formData.type == 2 ? 0 : formData.product_id) == 0">余额不足，无法进行操作</span>
                    
                <el-form-item 
                    v-if="getMoney(formData.type == 2 ? 0 : formData.product_id) > 0"
                    :label="'转' + (formData.type == 2 ? '入' : '出') + '金额'"
                    prop="amount"
                >
                    <a href="javascript:;" @click="formData.amount = (getMoney(formData.type == 2 ? 0 : formData.product_id))" class="all">全部转{{ (formData.type == 2 ? '入' : '出') }}</a>
                    <el-input type="number" placeholder="请输入金额" v-model="formData.amount"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button v-if="getMoney(formData.type == 2 ? 0 : formData.product_id) > 0" type="primary" :loading="submitLoading" @click="submitForm">确定</el-button>
            </span>
        </el-dialog>
    </div>

    
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { transfer } from '@/api/user'

const exchangeReloadTime = 2000;

export default {
    name: 'User-Exchange',
    props: ['type', 'user', 'product'],
    components: {
        
    },

    data() {
        var validateAmount = (rule, value, callback) => {
            if(value == '') {
                callback(new Error('金额不能为空'));
            }
            else if (isNaN(parseFloat(value))) {
                callback(new Error('金额必须为数字'));
            }else if (parseFloat(value) < 1) {
                callback(new Error('最少转入1元'));
            } else {
                callback();
            }
        }

        return {
            userMoney: {
                id: 0,
                name: '钱包余额',
                icon: '',
                money: this.user.userInfo.total_money,
            },
            pageLoading: false,
            submitLoading: false,
            btnLoading: {

            },
            dialogVisible: false,
            fromText: '',
            toText: '',

            formData: {
                type: 0,
                amount: '',
                product_id: 0,
            },

            formRules: {
                amount: [
                    { require: true, validator: validateAmount, trigger: 'blur'},
                ]
            }
        }
    },

    computed: {
        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });

            // list.sort((p, n) => {
            //     return this.getMoney(p.id) < this.getMoney(n.id) ? 1 : -1;
            // });

            return list;
        },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.reload();
        },

        reload(product_id) {
            if(this.pageLoading) return ;

            this.pageLoading = true;
            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
            this.$store.dispatch('productBalance', { product_ids }).then(() => {
                setTimeout(() => {
                    this.pageLoading = false;
                    // this.$message.success('余额同步完成');
                }, 500);
            });
        },

        productReload(product_id) {
            if(this.pageLoading) return ;

            this.pageLoading = true;
            this.$store.dispatch('productBalanceSync', { product_ids: product_id }).then((json) => {
                this.pageLoading = false;
                if(json.code == 200) {
                    this.$message.success('操作成功，同步余额已受理，请稍后查看金额');
                    setTimeout(() => {
                        this.reload();
                    }, 1500);
                }else {
                    this.$message.error(json.info);
                }
            });
        },

        getMoney(id) {
            let money = 0;

            if(id == 0) {
                money = this.user.userInfo.total_money;
            }else {
                for(const key in this.product.balance) {
                    if(key == id) {
                        money = this.product.balance[key].money;
                    }
                }
            }
            
            return Number(money).toFixed(2);
        },

        getTotalMoney() {
            let money = Number(this.user.userInfo.total_money);
            
            for(const key in this.product.balance) {
                if(Number(key) !== 0) {
                    money += Number(this.product.balance[key].money);
                }
            }

            return money.toFixed(2);
        },

        submitForm() {
            if(this.submitLoading) return ;

            this.$refs.form.validate((valid) => {
                if (valid) {
                    const data = Object.assign({}, this.formData);

                    data.amount = Number(data.amount);

                    const compareMoney = data.type == 2 ? this.getMoney(0) : this.getMoney(data.product_id);
                    if(data.amount > compareMoney) {
                        this.$message.error('余额不足');
                        return ;
                    }

                    this.submitLoading = true;
                    transfer(data).then(json => {
                        this.submitLoading = false;
                        if (json.code == 200) {
                            this.$message({
                                message: '操作成功，转账已受理，请稍后查看金额',
                                type: 'success',
                                customClass: 'long',
                                duration: 3000,
                                center: true,
                            });

                            setTimeout(() => {
                                this.reload();

                                setTimeout(() => {
                                    this.reload();
                                }, exchangeReloadTime)
                            }, exchangeReloadTime);
                            this.dialogVisible = false;
                        } else {
                            this.$message.error(json.info);
                        }
                    });
                }
            });
        },

        openForm(id, type) {
            this.formData.type = type;
            this.formData.product_id = id;
            this.formData.amount = '';

            if(type == 3) {
                if(this._getLoading(id)) return ;

                this.$confirm('确认全部回收吗?', '提示', {
                    type: 'warning'
                }).then(() => {
                    const data = {
                        type,
                        product_id: id ? id : this.filterProduct.map(item => { return item.id }).join(','),
                    }

                    this._setLoading(id, true);
                    transfer(data).then(json => {
                        this._setLoading(id, false);

                        if (json.code == 200) {
                            this.$message({
                                message: '操作成功，转账已受理，请稍后查看金额',
                                type: 'success',
                                customClass: 'long',
                                duration: 3000,
                                center: true,
                            });

                            setTimeout(() => {
                                this.reload();

                                setTimeout(() => {
                                    this.reload();
                                }, exchangeReloadTime)
                            }, exchangeReloadTime);

                        } else {
                            this.$message.error(json.info);
                        }
                    });
                }).catch(() => { });

                return ;
            }

            let product = this.product.list.filter(p => p.id == id );

            if(!product.length) return ;

            product = product[0];

            const productMoneyText = product.name + ' <span class="money">￥'+ this.getMoney(id) +'</span>';
            const moneyText = '钱包余额 <span class="money">￥'+ this.getMoney(0) +'</span>';

            if(type == 2) {
                this.toText = productMoneyText;
                this.fromText = moneyText;
            }else {
                this.fromText = productMoneyText;
                this.toText = moneyText;
            }
            
            this.dialogVisible = true;
        },

        _getLoading(id) {
            return this.btnLoading[id];
        },

        _setLoading(id, val) {
            const loading = Object.assign({}, this.btnLoading);
            loading[id] = val;
            this.btnLoading = loading;
        }
    }
}
</script>

