<template>
    <div class="user_index">
		<!-- <div role="alert" class="el-alert el-alert--warning is-light security_alert" v-if="(!user.userInfo.mobile_phone && !user.userInfo.email) && subNavShow">
			<div class="container">
				<div class="el-alert__content">
					<span class="el-alert__title">
						请绑定手机号或邮箱，以防止密码丢失或被盗&nbsp;&nbsp;
						<router-link to="/user/modify/phone">绑定手机号</router-link>&nbsp;
						<router-link to="/user/modify/email">绑定邮箱</router-link>
					</span>
				</div>
			</div>
		</div> -->

		<CommonTwoCol :user="user" :page="page" :product="product" :list="navList" :title="title">
			<!-- <template slot="title">
				<span v-if="page == 'modify'">
					<router-link to="/user/security">安全中心</router-link> / 
					<span class="color_gray">
						{{ title }}
					</span>
				</span>
				<span v-if="page != 'modify'">
					{{ title }}
				</span>
			</template> -->
			<template slot="main">
				<Level :user="user" :type="type" :product="product" v-if="page == 'level'" />
				<Info :user="user" :type="type" :product="product" v-if="page == 'info'" />
				<Security :user="user" :type="type" :product="product" v-if="page == 'security'" />
				<Modify :user="user" :type="type" :product="product" v-if="page == 'modify'" />
				<Bank :user="user" :type="type" :product="product" v-if="page == 'bank'" />
				<Charge :user="user" :type="type" :product="product" v-if="page == 'charge'" />
				<Withdraw :user="user" :type="type" :product="product" v-if="page == 'withdraw'" />
				<Transfer :user="user" :type="type" :product="product" v-if="page == 'transfer'" />
				<Exchange :user="user" :type="type" :product="product" v-if="page == 'exchange'" />
				<Order :user="user" :type="type" :product="product" v-if="page == 'order'" />
			</template>
        </CommonTwoCol>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'

import CommonTwoCol from '@/views/layout/components/CommonTwoCol/Index'

import Level from './components/Level'
import Info from './components/Info'
import Security from './components/Security'
import Modify from './components/Modify'
import Bank from './components/Bank'
import Charge from './components/Charge'
// import Agent from './components/Agent'
import Withdraw from './components/Withdraw'
import Transfer from './components/Transfer'
import Exchange from './components/Exchange'
import Order from './components/Order'

export default {
    name: 'User-Index',
    components: {
		CommonTwoCol,
		Level,
		Info,
		Security,
		Modify,
		Bank,
		Charge,
		Withdraw,
		Transfer,
		Exchange,
		Order,
		// Agent,
    },

    data() {
        return {
			subNavShow: true,
			navList: [
                {
                    'name': '个人中心', 
                    'icon': 'fa fa-user',
                    'subList': [
                        {
                            'name': '会员等级',
                            'link': '/user',
                            'page': ['level'],
                    		'icon': require('@/assets/user/user_vip.png.webp'), 
                        },
                        {
                            'name': '个人资料',
                            'link': '/user/info',
                            'page': ['info'],
                    		'icon': require('@/assets/user/user_profile.png.webp'), 
                        },
                        // {
                        //     'name': '安全中心',
                        //     'link': '/user/security',
                        //     'page': ['security', 'modify'],
                        // },
                        // {
                        //     'name': '银行卡管理',
                        //     'link': '/user/bank',
                        //     'page': ['bank'],
                        // },
                    ]
                },
				{
                    'name': '财务中心', 
                    'icon': 'fa fa-credit-card', 
                    'subList': [
                        {
                            'name': '存款',
                            'link': '/user/charge',
                            'page': ['charge'],
                    		'icon': require('@/assets/user/user_deposit.png.webp'), 
                        },
                        {
                            'name': '取款',
                            'link': '/user/withdraw',
                            'page': ['withdraw'],
                    		'icon': require('@/assets/user/user_withdraw.png.webp'), 
						},
						{
                            'name': '投注记录',
                            'link': '/user/order',
                            'page': ['order'],
                    		'icon': require('@/assets/user/user_betting.png.webp'), 
                        },
                        {
                            'name': '资金记录',
                            'link': '/user/transfer',
                            'page': ['transfer'],
                    		'icon': require('@/assets/user/user_transaction.png.webp'), 
                        },
                        {
                            'name': '转账',
                            'link': '/user/exchange',
                            'page': ['exchange'],
                    		'icon': require('@/assets/user/user_transfer.png.webp'), 
                        },
                    ]
                },
                // {
                //     'name': '投注管理', 
                //     'icon': 'fa fa-list-alt', 
                //     'subList': [
                //         {
                //             'name': '投注记录',
                //             'link': '/user/order',
                //             'page': ['order'],
                //         }
                //     ]
                // },
                // {
                //     'name': '资金管理', 
                //     'icon': 'fa fa-credit-card', 
                //     'subList': [
                //         {
                //             'name': '资金充值',
                //             'link': '/user/charge',
                //             'page': ['charge'],
                //         },
                //         {
                //             'name': '资金提款',
                //             'link': '/user/withdraw',
                //             'page': ['withdraw'],
                //         },
                //         {
                //             'name': '账户转账',
                //             'link': '/user/exchange',
                //             'page': ['exchange'],
                //         },
                //         {
                //             'name': '资金记录',
                //             'link': '/user/transfer',
                //             'page': ['transfer'],
                //         }
                //     ]
                // },
			],

			loading: false,
        }
	},

	computed: {
		user() { return this.$store.getters.user },
		product() { return this.$store.getters.product },
		page() { return this.$route.params.page || 'level' },
		type() { return this.$route.params.type },

		title() {
			const titleArr = {};

			this.navList.forEach(nav => {
				nav.subList.forEach(sub => {
					sub.page.forEach(page => {
						titleArr[page] = sub.name;
					})
				})
			})

			const page = this.page;
			let title = titleArr[page];

			if (page == 'modify') {
				switch(this.type) {
					case 'password':
						title += '密码';
					break;
					case 'security_code':
						if(this.user.userInfo.has_draw_passwd) {
							title += '提款密码';
						}else {
							title = '设置提款密码';
						}
					break;
					case 'find_security_code':
						title = '找回提款密码';
					break;
					case 'phone':
						if(this.user.userInfo.mobile_phone) {
							title += '手机号';
						}else {
							title = '绑定手机号';
						}
					break;
					case 'email':
						if(this.user.userInfo.email) {
							title += '邮箱';
						}else {
							title = '绑定邮箱';
						}
					break;
				}
			}

			if (page == 'agent') {
				switch(this.type) {
					case 'invite':
						title += '开户';
					break;
				}
				if(!this.type) {
					title += '说明'
				}
			}

			return title;
		}
	},

    mounted() {
		this.init()
		// console.log(this.navList);
    },

    methods: {
        init() {
            
		},

    }
}
</script>

