<template>
    <div class="user_transfer">

        <div class="filter">
            <label>类型：</label>
            <el-select size="small" clearable v-model="formData.type" placeholder="请选择" class="type_select">
                <el-option
                    v-for="item in typeOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select> &nbsp;
            <label>日期：</label>
            <el-date-picker size="small"
                v-model="formData.date"
                type="daterange"
                align="left"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="{ shortcuts: pickerOptions }"
            >
            </el-date-picker>
            &nbsp;<el-button size="small" type="primary" @click="filterSubmit">筛选</el-button>
            <el-button size="small" plain @click="requestTrade" icon="el-icon-refresh">刷新</el-button>
        </div>

        <div 
        >
            <el-table 
            v-loading="loading"
            :element-loading-text="'加载中'"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
            size="small"
            :data="tradeList.data"
            style="width: 100%">
                <el-table-column
                    prop="trade_no"
                    label="订单号"
                    width="220"
                ></el-table-column>
                <el-table-column
                    prop="type_text"
                    label="类型"
                ></el-table-column>
                <el-table-column
                    prop="amount"
                    label="金额"
                >
                    <template slot-scope="scope">
                        <span :class="{color_green: isAmountInput(scope.row), color_red: !isAmountInput(scope.row) }">
                            <span v-if="scope.row.commission == 0">{{ (isAmountInput(scope.row) ? '+' : '-') + '￥' }}{{ scope.row.amount | moneyToFixed }}</span>
                            
                            <span v-if="scope.row.commission != 0">{{ (isAmountInput(scope.row) ? '+' : '-') + '￥' + (Number(scope.row.amount)).toFixed(2) }}</span>
                            <div v-if="scope.row.commission != 0">(手续费{{ scope.row.commission | moneyToFixed }})</div>
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="current_total"
                    label="钱包余额"
                >
                    <template slot-scope="scope">
                        <span v-if="scope.row.status == 2">
                            ￥{{ scope.row.current_total | moneyToFixed }}
                        </span>
                        <span v-else>
                            -
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="acount_code_text"
                    label="交易相关"
                    width="150"
                >
                    <template slot-scope="scope">
                        <span class="info_row">
                            {{ getAccoutCodeText(scope.row)}}
                            <p v-if="scope.row.desc">备注：{{ scope.row.desc }}</p>
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="status_text"
                    label="状态"
                >
                    <template slot-scope="scope">
                        <!-- <span :class="getClass(scope.row)">
                            {{ scope.row.status_text }}
                        </span> -->
                        <el-tag size="small" :type="getType(scope.row.status)" effect="dark">{{ scope.row.status_text }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="created_at"
                    label="创建日期"
                    width="115"
                >
                    <template slot-scope="scope">
                        {{ parseTime(scope.row.created_at, '{m}-{d} {h}:{i}:{s}') }}
                    </template>
                </el-table-column>
                <el-table-column
                    prop="modify_at"
                    label="修改日期"
                    width="115"
                >
                    <template slot-scope="scope">
                        {{  parseTime(scope.row.modify_at, '{m}-{d} {h}:{i}:{s}') }}
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                background
                layout="prev, pager, next"
                @current-change="pageChange"
                :page-count="tradeList.total_pages"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { pickerOptions, formatTime, parseTime, formatBankAcount } from '@/utils/index'
import { trade } from '@/api/user'

export default {
    name: 'User-Transfer',
    props: ['type', 'app', 'user', 'product'],
    components: {
        
    },

    data() {
        return {
            tradeList: {},
            loading: false,
            formData: {
                date: '',
                type: '0',
                page: 1,
            },
            typeOptions: [
                { value: '0', label: '所有类型' },
                { value: '1', label: '充值' },
                { value: '2', label: '提现' },
                { value: '3', label: '钱包转入' },
                { value: '4', label: '钱包转出' },
                { value: '5', label: '系统上分' },
                { value: '6', label: '系统返水发放' },
                { value: '7', label: '代理返水发放' },
                { value: '8', label: '系统减分' },
                { value: '9', label: '活动赠送' },
                { value: '18', label: '代理转入' },
            ]
        }
    },

    computed: {
        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });

            return list;
        },

        
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.requestTrade();

            // 同步一次余额
            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
            this.$store.dispatch('productBalance', { product_ids })
        },

        requestTrade() {
            if(this.loading) return ;
            const data = {
                type: this.formData.type,
                // date: this.formData.date,
                page: this.formData.page,
            }

            if(this.formData.date && this.formData.date.length == 2) {
                data.start_at = parseInt(+new Date(this.formData.date[0]))/1000;;
                data.end_at = parseInt(+new Date(this.formData.date[1]))/1000;;
            }

            this.loading = true;
            trade(data).then(json => {
                this.loading = false;
				if (json.code == 200) {
                    this.tradeList = json.list;
                    
				}else if(json.code == 10002) {
                    this.tradeList = {};
                }
			});
        },

        isAmountInput(row) {
            const type = Number(row.type);

            if([1, 3, 5, 6, 7, 9, 18].indexOf(type) > -1) {
                return true;
            }

            return false;
        },

        getAccoutCodeText(row) {
            let text = '';

            switch(row.type) {
                case '1':
                    text = row.acount.type_text + '充值';
                break;
                case '2':
                    text = '提现至银行卡号：' + row.acount.acount;
                break;
                case '3':
                    text = row.product.name + ' > 钱包';
                break;
                case '4':
                    text = '钱包 > ' + row.product.name;
                break;
                case '5':
                    text = '系统上分' +(row.sub_type_text ? ('-' + row.sub_type_text) : '');
                break;
                case '6':
                    text = '系统返水发放';
                break;
                case '7':
                    text = '代理返水发放';
                break;
                case '8':
                    text = '系统减分';
                break;
                case '9':
                    text = row.activity.name;
                break;
                case '18':
                    text = '代理转入';
                break;
            }

            return text;
        },

        filterSubmit() {
            this.requestTrade();
        },

        pageChange(currentPage) {
            this.formData.page = currentPage;
            this.requestTrade();
        },

        getType(status) {
            let type;
            if(status == 0 || status == 1) {
                   type = 'warning';
            }else if(status == 2) {
                   type = 'success';
            }else if(status == 3 || status == 4) {
                   type = 'danger';
            }else if(status == 5 || status == 6) {
                   type = 'info';
            }

            return type;
        },

        parseTime(time, format) {
            return parseTime(time, format);
        }
    }
}
</script>

