<template>
    <div class="common_userinfo">
        <el-row :gutter="20">
            <el-col :span="10">
                <div class="grid-content bg-purple">
                    <el-card class="box-card tc_card">
                        <!-- <span>用户名：{{ user.userInfo.username }}</span>&nbsp;&nbsp; -->
                        <span>总资产：<MoneyDropdown :user="user" :product="product" color="#ffde00" /></span>
                        <span class="m_handler">
                            <router-link to="/user/charge">充值</router-link>
                            <router-link to="/user/withdraw">提现</router-link>
                        </span>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="14">
                <div class="grid-content bg-purple">
                    <el-card class="box-card tc_card">
                        <span class="secure_level">
                            账户安全等级：<span v-html="getSecureLevel"></span>&nbsp;&nbsp;
                            <span style="font-size: 12px;">
                                <router-link v-if="!user.userInfo.has_draw_passwd" to="/user/info?t=secure">设置提款密码</router-link>
                                &nbsp;
                                <router-link v-if="!user.bankcardList.length" to="/user/info?t=bank">绑定银行卡</router-link>
                                &nbsp;
                                <router-link v-if="!user.userInfo.mobile_phone" to="/user/info?t=phone">绑定手机号</router-link>
                                &nbsp;
                                <router-link v-if="!user.userInfo.email" to="/user/info/?t=email">绑定邮箱</router-link>
                            </span>
                        </span>
                    </el-card>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import MoneyDropdown from '@/views/layout/components/MoneyDropdown'
import { imgFormatter } from '@/utils/index'

export default {
    name: 'common_userinfo',
    props: ['user', 'product'],
    components: {
        MoneyDropdown
    },

    data() {
        return {
            levelImg: [
                {
                    url: imgFormatter('/images/2019/09/08/c2626222-dc1e-40b5-be65-e7cbd6e59192.jpg')
                }
            ],
        }
    },

    computed: {
        getSecureLevel() {
            let levelCount = 0;

            if(this.user.userInfo.has_draw_passwd) {
                levelCount += 2;
            }
            
            if(this.user.userInfo.mobile_phone) {
                levelCount++;
            }
            
            if(this.user.userInfo.email) {
                levelCount++;
            }

            if(levelCount <= 1) {
                return '<span style="color: #F56C6C;">低</span>';
            }else if(levelCount <= 2) {
                return '<span style="color: #E6A23C;">中</span>';
            }else if(levelCount > 2) {
                return '<span style="color: #67C23A;">高</span>';
            }

        }
    },

    mounted() {
        
    },

    methods: {
        
    }
}
</script>

