<template>
    <div class="user_bank">
        <div class="form-group" v-if="!user.userInfo.has_draw_passwd">
                <p class="form-control-plaintext">
                    为了资金安全，请先设置
                    <router-link :to="{ path: '/user/modify/security_code', query: { r: '/user/bank' }}" class="ml-1" style="text-decoration: underline;">
                    设置提款密码
                    </router-link>
                </p>
        </div>
        <div v-if="user.userInfo.has_draw_passwd" v-loading="loading">
            <div>
                <div v-if="user.bankcardList.length" style="color: #cc5151; font-size: 12px;">如需修改或删除银行卡请联系客服</div>
                <div class="bank_section rounded" :key="card.id" v-for="card in user.bankcardList">
                    <div>{{ card.open_account_bank }}（{{ card.name }}）</div>
                    <div class="number">{{ card.bank_account | cardNumberFilter }}</div>

                    <!-- <el-dropdown trigger="click">
                        <span class="el-dropdown-link">
                            <i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item>
                                <div @click="bankEdit(card)">编辑</div>
                            </el-dropdown-item>
                            <el-dropdown-item>
                                <div @click="bankDel(card)">删除</div>
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown> -->
                </div>
                <a href="javascript:;" @click="bankCreate" v-if="(!user.bankcardList.length)" class="bank_section rounded add_new">
                    绑定银行卡
                </a>
            </div>
        </div>

        <el-dialog
            :title="(dialogType == 'craete' ? '绑定' : '编辑') + '银行卡'"
            :visible.sync="addBankCardShow"
            width="30%"
        >
            <div class="new_card">
                <el-form ref="bankForm" :model="form" :rules="formRules" label-width="100px" label-position="top">
                    <el-form-item label="开户银行" prop="open_account_bank">
                        <el-select v-model="form.open_account_bank" placeholder="请选择">
                            <el-option
                            v-for="name in bank_list"
                            :key="name"
                            :label="name"
                            :value="name">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="开户城市" prop="areaSelect">
                        <el-cascader
                            change-on-select
                            expand-trigger="hover"
                            :options="areaOptions"
                            v-model="form.areaSelect"
                        >
                        </el-cascader>
                    </el-form-item>
                    <el-form-item label="银行卡号" prop="bank_account">
                        <el-input v-model="form.bank_account" placeholder="请输入银行卡号"></el-input>
                    </el-form-item>

                    <el-form-item label="开户姓名" prop="name">
                        <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
                    </el-form-item>

                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addBankCardShow = false">取 消</el-button>
                <el-button type="primary" @click="addBankCardSubmit" :loading="loading">提 交</el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { getAddr } from '@/api/common'
import { 
    bankAccountValidator,
    bankNameValidator,
} from '@/utils/validator'

export default {
    name: 'User-Bank',
    props: ['type', 'user'],
    components: {

    },

    filters: {
        cardNumberFilter(number) {
            const arr = String(number).split('');
            var result = [];
            for(var i=0, len=arr.length; i<len; i+=4){
                result.push(arr.slice(i,i+4));
            }

            return result.map(s => s.join('')).join(' ');
        }
    },

    data() {
        var validateArea = (rule, value, callback) => {
            if (!value || value.length != 2) {
                callback(new Error('请选择开户城市'));
            } else {
                callback();
            }
        };

        return {
            dialogType: 'create',
            loading: false,
            delBankCardLoading: false,

            addBankCardShow: false,
            bank_list: [
                '工商银行', '建设银行', '农业银行', '民生银行', '招商银行', '交通银行', '中国银行', '邮政储蓄', '中信银行', '兴业银行', '华夏银行', '浦发银行', '广发银行', '平安银行', '光大银行', '农商银行', '台州银行'
            ],
            areaOptions: [],
            loading: false,
            form: {
                id: '',
                open_account_bank: '',
                areaSelect: [],
                bank_account: '',
                name: '',
            },
            formRules: {
                open_account_bank: [{ required: true, message: '请选择开户银行', trigger: 'change' }],
                areaSelect: [{ required: true, validator: validateArea, trigger: 'change' }],
                bank_account: [{ required: true, message: '请选择银行卡号', trigger: 'blur' }],
                bank_account: bankAccountValidator,
                name: bankNameValidator,
            },
        }
    },

    computed: {

    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            if(!this.user.bankcardList.length) {
                this.getBankcardList();
            }

            if(this.areaOptions.length == 0) {
                getAddr().then(json => {
                    if (json.code == 200) {
                        const areaOptions = json.list;
                        
                        areaOptions.forEach(province => {
                            province.value = province.label = province.name;
                            province.children = province.cities;

                            province.children.forEach(city => {
                                city.value = city.label = city.name;
                            });
                        });
                        
                        this.areaOptions = areaOptions;
                    }
                })
            }
        },

        getBankcardList() {
            if(this.loading) return;
            this.loading = true;
            this.$store.dispatch('bankcardList').then(() => {
                this.loading = false;
            })
        },

        bankCreate() {
            this.form = {
                id: '',
                open_account_bank: '',
                areaSelect: [],
                bank_account: '',
                name: '',
            }

            this.dialogType = 'create';

            this.addBankCardShow = true;
        },

        bankEdit(card) {
            this.$alert('如需修改或删除银行卡请联系客服', '', {
                confirmButtonText: '确定',
                callback: action => {
                    
                }
            });
            return ;

            this.form.id = card.id;
            this.form.open_account_bank = card.open_account_bank;
            this.form.areaSelect = card.open_account_province.split('-');
            this.form.bank_account = card.bank_account;
            this.form.name = card.name;
            this.dialogType = 'update';

            this.addBankCardShow = true;
        },

		bankDel(card) {
            this.$alert('如需修改或删除银行卡请联系客服', '', {
                confirmButtonText: '确定',
                callback: action => {
                    
                }
            });
            return ;

			if(!confirm('确认删除吗')) return ;
            
            if(this.delBankCardLoading) return ;
            this.delBankCardLoading = true;
            this.$store.dispatch('delBankCard', card.id).then(json => {
                this.delBankCardLoading = false;
                if (json.code == 200) {
                    this.$message.success('删除成功');
                    this.getBankcardList();
				} else {
                    this.$message.error(json.info);
				}
            });
        },
        
        addBankCardSubmit() {
            this.$refs.bankForm.validate((valid) => {
                if (valid) {
                    var data = {
                        id: this.form.id,
                        name: this.form.name,
                        open_account_bank: this.form.open_account_bank,
                        bank_account: this.form.bank_account,
                        open_account_province: this.form.areaSelect[0] + '-' + this.form.areaSelect[1],
                        open_account_address: '',
                        id_card: '',
                    }

                    if(this.loading) return ;
                    this.loading = true;
                    this.$store.dispatch('addBankcard', data).then(json => {
                        this.loading = false;

                        if (json.code == 200) {
                            this.$message.success((this.dialogType == 'create' ? '添加' : '修改') + '成功');
                            this.form = {
                                id: '',
                                open_account_bank: '',
                                areaSelect: [],
                                bank_account: '',
                                name: '',
                            };
                            this.$store.dispatch('bankcardList');
                            this.addBankCardShow = false;
                        } else {
                            this.$message.error(json.info);
                        }
                    })
                }
            })
        }
    }
}
</script>

