<template>
    <div class="user_withdraw">
        <div class="form-group" v-if="!user.userInfo.has_draw_passwd">
                <p class="form-control-plaintext">
                    为了资金安全，请先设置
                    <router-link :to="{ path: '/user/info?t=secure' }" class="ml-1" style="text-decoration: underline;">
                    设置提款密码
                    </router-link>
                </p>
        </div>
        <div v-else-if="!tradeLoading">
            <div v-if="highoddsInfo" v-html="highoddsInfo" style="padding-bottom: 10px;color: #ea3535;font-size: 16px;">
            </div>

            <div v-if="hasTradeList" style="padding: 15px 0;">
                您有待处理中的取款，请耐心等待客服审核，前往查看 <router-link to="/user/transfer">资金列表</router-link>
            </div>
            <div v-else>
                <!-- <div style="padding: 15px 0;color: #ff9800;">
                    
                </div> -->
                <el-form :model="formData" :rules="formRules" ref="withdrawForm" label-width="110px" label-position="left">
                    <el-form-item 
                    label="钱包余额：">
                        <span class="common_money_red">
                            ￥{{ user.userInfo.total_money }}
                        </span>
                    </el-form-item>
                    <el-form-item 
                        v-if="user.bankcardList.length"
                        label="提款金额："
                        prop="amount"
                    >
                    <el-input ref="amount" v-model.number="formData.amount" placeholder="请输入提款金额，最低提现100元"></el-input>
                    </el-form-item>
                    <el-form-item label="提款银行卡：" prop="bankcard_id">
                        <div>
                            <el-select v-model="formData.bankcard_id" placeholder="请选择" v-if="user.bankcardList.length">
                                <el-option
                                v-for="bank in user.bankcardList"
                                :key="bank.id"
                                :label="bank.open_account_bank + ', ' + bank.bank_account + ', ' + bank.name"
                                :value="bank.id">
                                </el-option>
                            </el-select>
                            <router-link to="/user/info?t=bank" v-if="!user.bankcardList.length">请先绑定银行卡</router-link>
                            <!-- <a href="javascript:;" @click="$store.dispatch('openAddBankCardDialog')" v-if="!user.bankcardList.length">请先绑定银行卡</a> -->
                        </div>
                        
                    </el-form-item>
    
                    <el-button type="primary" :loading="loading" @click="checkInfo" v-if="user.bankcardList.length">提交</el-button>
                </el-form>
                
            </div>

            <el-dialog
                title="提示"
                class="check_dialog"
                :visible.sync="checkDialogVisible"
                width="50%"
            >
                <h3>
                    <i class="el-icon-warning"></i>
                    您的流水未到达提款要求，如需提款请联系客服

                    <!-- 您的流水未到达提款要求，继续提款将会扣除 <span style="color: #F44336;">{{ (Number(formData.amount) * Number(checkList.check_fee) * 0.01).toFixed(2) }}</span> 元 -->
                    <!-- （{{ checkList.check_fee }}%） -->
                    <!-- 手续费，是否继续申请提款？（如有疑问请联系客服） -->
                </h3>
                    
                <el-table
                    :data="checkList.items"
                    style="width: 100%">
                    <el-table-column
                        prop="created_at"
                        label="时间">
                        <template slot-scope="scope">
                            {{ formatTime(scope.row.created_at) }}
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="type"
                        label="类型">
                        <template slot-scope="scope">
                            {{ checkType[scope.row.type] }}
                        </template>
                    </el-table-column>

                    <el-table-column
                        prop="recharge_price"
                        label="金额">
                        <template slot-scope="scope">
                            ￥{{ scope.row.recharge_price }}
                        </template>
                    </el-table-column>

                    <el-table-column
                        prop="recharge_price"
                        label="存款稽核点">
                        <template slot-scope="scope">
                            {{ scope.row.stream_price }} / {{ scope.row.recharge_price * scope.row.check }}
                        </template>
                    </el-table-column>
                    
                    <el-table-column
                        prop="is_ok"
                        label="状态">
                        <template slot-scope="scope">
                            <el-tag :type="scope.row.is_ok == 'Y' ? 'success' : 'danger'">
                                {{ scope.row.is_ok == 'Y' ? '满足' : '未满足' }}
                            </el-tag>
                        </template>
                    </el-table-column>
                </el-table>

                <span slot="footer" class="dialog-footer">
                    <el-button @click="checkDialogVisible = false">取 消</el-button>
                    <!-- <el-button type="primary" :loading="loading" @click="showCheckDrawPassword">继续申请提款</el-button> -->
                </span>
            </el-dialog>

        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { withdraw, checkInfo, checkDrawpassword, judegehighodds } from '@/api/user'
import { formatTime } from '@/utils/index'
import { trade } from '@/api/user'

const minWithdraw = 100;

export default {
    name: 'User-Withdraw',
    props: ['type', 'user', 'product'],
    components: {

    },

    data() {

        return {
            formData: {
                bankcard_id: '',
                amount: '',
            },
            formRules: {
                amount: [
                    { required: true, message: '金额不能为空', trigger: 'blur'},
                    { type: 'number', message: '金额必须为数字', trigger: 'blur'},
                    { type: 'number', min: 100, message: '最低提现'+ minWithdraw +'元', trigger: 'blur' },
                    { type: 'number', max: Number(this.user.userInfo.total_money), message: '不能超过您的钱包余额', trigger: 'blur' },
                ],
                bankcard_id: [
                    { required: true, message: '银行不能为空', trigger: 'blur'},
                ]
            },
            loading: false,
            checkDrawLoading: false,
            checkDialogVisible: false,
            checkList: {},
            highoddsInfo: null,
            checkType: {
                'recharge': '充值',
                'stream': '返水',
                'activity': '活动',
                'highodds': '高赔率活动稽核',
            },
            tradeLoading: true,
            hasTradeList: false,
        }
    },

    computed: {

    },

    mounted() {
        // if(this.$refs.amount) {
        //     this.$refs.amount.$el.focus()
        // }

        this.init();
    },

    methods: {
        init() {
            if(!this.user.bankcardList.length) {
                this.$store.dispatch('bankcardList').then(() => {
                    if(this.user.bankcardList.length) {
                        this.formData.bankcard_id = this.user.bankcardList[0].id;
                    }
                });
            }else {
                this.formData.bankcard_id = this.user.bankcardList[0].id;
            }

            // judegehighodds().then(json => {
            //     if(json.code == 200) {
            //         const list = json.list;

            //         let productText;
            //         if(list.productIds.length) {
            //             productText = this.product.list.filter(item => {
            //                 return list.productIds.indexOf(item.id) > -1;
            //             }).map(item => {
            //                 const link = this.$router.resolve('/p?id=' + item.id).href;
            //                 return `<a href="${link}" target="_blank">${item.name}</a>`;
            //             }).join(', ');
            //         }else {
            //             productText = '其他游戏投注';
            //         }

            //         this.highoddsInfo = `<span>因您玩过高倍彩票，需要到 ${ productText }, 玩够 ${ list.recharge_price } 流水（已经有流水: ${ list.stream_price }），否则提现需要收取一定比例手续费</span>`;
            //     }
            // })

            this.checkTrade();
        },

        // 检查是否有进行中的提款
        checkTrade() {
            const data = {
                type: 2,
                page: 1,
                count: 1,
                status: 0,
            }

            this.tradeLoading = true;
            trade(data).then(json => {
                this.tradeLoading = false;
				if (json.code == 200) {
                   this.hasTradeList = !!json.list.data.length;
				}
			});
        },

        showCheckDrawPassword() {
            this.$refs.withdrawForm.validate((valid) => {
                if (valid) {
                    if(this.checkDrawLoading) return ;
                    this.$prompt('请输入6位提款密码', '提示', {
                        confirmButtonText: '确定',
                        inputType: 'password',
                        cancelButtonText: '取消',
                        inputPattern: /[0-9]{6}/,
                        inputErrorMessage: '提款密码格式不正确',
                        beforeClose: (action, instance, done) => {
                            if (action === 'confirm') {
                                instance.confirmButtonLoading = true;
                                instance.confirmButtonText = '提交中...';
                                this.checkDrawLoading = true;
                                const value = instance.$refs.input.value;
                                this.submitForm(value, () => {
                                    done();
                                }, () => {
                                    instance.confirmButtonLoading = false;
                                    instance.confirmButtonText = '提交';
                                });
                            } else {
                                done();
                            }
                        }
                    }).then(({ value }) => {
                        // this.checkDrawLoading = true;
                        // this.submitForm(value);
                        // checkDrawpassword(value).then(json => {
                        //     if(json.code == 200) {
                        //         this.submitForm();
                        //     }else {
                        //         this.$message.error('提款码错误');
                        //     }
                        //     this.checkDrawLoading = false;
                        // })
                    }).catch(() => {
                        
                    });
                }
            });
        },

        // force 是否 未达到流水还继续提款
        submitForm(draw_passwd, success, error) {
            if(this.loading) return ;

            this.$refs.withdrawForm.validate((valid) => {
                if (valid) {
                    const data = {
                        bankcard_id: this.formData.bankcard_id,
                        amount: this.formData.amount,
                        draw_passwd,
                    }

                    if(data.amount > this.user.userInfo.total_money) {
                        this.$message.error('不能超过您的总额');
                        return ;
                    }

                    // console.log(data);return ;

                    this.loading = true;
                    withdraw(data).then(json => {
                        if (json.code == 200) {
                            success && success();
                            this.$message.success('订单提交成功');
                            this.$router.push({ path: '/user/transfer' })
                            this.loading = false;
                        } else if(json.code == 1004) {
                            this.checkInfo();
                        }
                        else {
                            error && error();
                            this.$message.error(json.info);
                            this.loading = false;
                        }
                    });
                }
            });
        },

        formatTime(t) {
            return formatTime(t);
        },

        checkInfo() {
           if(this.loading) return ;

           this.$refs.withdrawForm.validate((valid) => {
                if(valid) {

                    this.loading = true;
                    checkInfo().then(json => {
                        this.loading = false;
                        if (json.code == 200) {
                            if(json.list.items.length > 0) {
                                this.checkList = json.list;
                                this.checkDialogVisible = true;    
                            }else {
                                this.showCheckDrawPassword();
                            }
                        }
                        else {
                            this.$message.error(json.info);
                        }

                    });
                }
            });
        }
    }
}
</script>

<style scoped lang="scss">

.user_withdraw {

	.el-card {
        display: inline-block;
        margin-right: 14px;
    }

    .el-select {
        width: 350px;
    }
}

.check_dialog {

    .el-dialog__body {
        padding: 15px 20px 10px;
    }

    i {
        font-size: 22px;
        color: #FF9800;
    }
}


</style>
