<template>
    <div class="user_charge"
    >
        <div v-if="emptyList">
            充值渠道暂时关闭
        </div>
        <div v-else class="charge_wrap">
            <el-form ref="mainForm" :model="formData" :rules="formRules" :label-position="labelPosition" :label-width="labelWidth">
                <el-form-item class="pay_methods" label="付款方式：">
                    <div>
                        <el-button size="small" :class="{ active: payChannel && payChannel.id == channel.id, sBtn: 1 }" @click="payChannelChange(channel)" :key="channel.id" v-for="channel in payChannelShowList">
                            <img :src="getChannelImg(channel) | imgFormatter" />
                            <div>{{ channel.name }}</div>
                            <i class="right"></i>
                        </el-button>
                    </div>
                </el-form-item>

                <el-form-item label="优惠活动：" prop="activityId" class="sc_check" v-if="activityList.length && payChannel.max >= 200">
                    <el-radio-group :max="1" v-model="formData.activityId">
                        <span :key="item.id" v-for="item in activityList" style="margin-right: 10px;" >
                            <el-radio :label="item.id" name="activityId">{{ item.name }}</el-radio>
                            <a href="javascript:;" v-if="item.type == 'SC'" @click="$store.dispatch('openScActivityDialog')">
                                <i class="el-icon-question"></i>
                            </a>
                        </span>
                        &nbsp;
                        <span>
                            <el-radio :label="0" name="activityId">不参与</el-radio>
                        </span>
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="存款金额：" prop="amount" class="form_amount">
                    <div v-if="payChannel && payChannel.id != 1">
                        <el-input v-model.number="formData.amount" placeholder="请输入金额"></el-input> <span class="tip" v-if="payChannel">单笔存款金额：{{ payChannel.min + ' - ' + payChannel.max }} 元</span>
                    </div>

                    <div style="margin-top: 5px;">
                        <el-button size="small" :class="{ active: formData.amount == amount, sBtn: 1 }" v-for="amount in amoutList" @click="amountClick(amount)">
                            {{ amount }}
                            <i class="right"></i>
                        </el-button>
                    </div>
                </el-form-item>

                <el-form-item label="转账信息：" v-if="payChannel.id == 5">
                    <div class="pay_detail">
                        <div>
                            <el-form label-width="90px" label-position="left">
                                <div class="info">
                                    <el-form-item label="转账银行：">
                                        <span>{{ cardInfo.open_account_bank }}</span>
                                    </el-form-item>
                                    <el-form-item label="转账户主：">
                                        <span>{{ cardInfo.name }}</span>
                                    </el-form-item>
                                    <el-form-item label="转账账号：">
                                        <span>{{ cardInfo.bank_account }}</span>
                                    </el-form-item>
                                    <el-form-item label="开户支行：">
                                        <span>{{ cardInfo.open_account_address }}</span>
                                    </el-form-item>
                                </div>
                            </el-form>
                        </div>
                    </div>
                </el-form-item>

                <el-form-item label="扫码支付：" v-if="checkIsShowProp('qrcode')">
                    <div class="">
                        <img :src="payChannel.qrcodeImg | imgFormatter" style="max-width: 300px;" />
                    </div>
                    <div v-if="payChannel.id == 7" class="qrcode_tip">
                        <div><span style="color: #f56c6c;">
                            <i class="el-icon-warning"></i>
                            请仔细阅读操作指南：</span></div>
                        <p>1. 微信扫码进入小程序，购买充值金额的对应产品，流程与淘宝购物基本一致；</p>
                        <p>2. 第一次使用时需要填写姓名、地址、手机号等信息，任意填写即可，不需要真实信息；</p>
                        <p>3. 购买完成后，返回平台充值页面，在小程序码下方填写您注册微信的手机号，然后提交即可。</p>
                        <p>4. 在填写地址等信息时候，可在该页面将您填写的地址设置为默认地址，下次使用即可不需要再次填写</p>
                    </div>
                </el-form-item>
            </el-form>

            <el-form ref="nameForm" :model="formData" :rules="formRules" :label-position="labelPosition" :label-width="labelWidth">
                <el-form-item label="存款姓名：" v-if="checkIsShowProp('name')" prop="name">
                    <el-input v-model="formData.name" placeholder="请输入存款姓名"></el-input>
                    &nbsp;
                    <span v-if="historyName.length" style="font-size: 12px; color: #d0c7c7;">
                        历史姓名：<a style="margin-right: 5px;" href="javascript:;" @click="formData.name = name;$refs['nameForm'].validateField('name');" :key="index" v-for="(name, index) in historyName">{{ name }}</a>
                        <!-- <a href="javascript:window.localStorage.setItem('historyName', '');" style="color: #797979;">清空</a> -->
                    </span>
                </el-form-item>
            </el-form>

             <el-form ref="codeForm" :model="formData" :rules="formRules" :label-position="labelPosition" :label-width="labelWidth">
                <el-form-item label="支付凭证后5位：" v-if="checkIsShowProp('code')" prop="code">
                    <el-input v-model="formData.code" placeholder="请输入支付凭证或支付订单后5位"></el-input>
                </el-form-item>
            </el-form>

             <el-form ref="phoneForm" :model="formData" :rules="formRules" :label-position="labelPosition" :label-width="labelWidth">
                <el-form-item label="手机号：" v-if="checkIsShowProp('phone')" prop="phone">
                    <el-input v-model="formData.phone" placeholder="请输入微信绑定的手机号"></el-input>
                </el-form-item>
            </el-form>

            <!-- <el-form ref="acountForm" :model="formData" :rules="formRules" :label-position="labelPosition" :label-width="labelWidth">

                <el-form-item label="支付宝账号：" v-if="payChannel.id == 4" prop="acount">
                    <el-input v-model="formData.acount" placeholder="请输入支付宝账号"></el-input>
                </el-form-item>

            </el-form> -->

            <el-form :label-position="labelPosition" :label-width="labelWidth">
                <el-form-item>
                    <el-button type="primary" @click="submitForm" :loading="loading">提交</el-button>
                </el-form-item>
            </el-form>
        </div>
        
        <el-dialog
            class="pay_status_dialog"
            :title="paymentInfo.isNew ? '扫描二维码支付' : '您有未完成的支付订单'"
            :visible.sync="paymentInfo.payStatusDialogVisible"
            width="700px"
        >
            <div class="text">
                <p style="font-size: 12px; padding-bottom: 0 0 5px;" v-if="paymentInfo.trade_no">订单号：{{ paymentInfo.trade_no }}</p>
                <p style="margin: 15px 0 0;" v-if="!paymentInfo.isNew && !paymentInfo.showCode">
                    <a href="javascript:;" @click="paymentInfo.showCode = true" style="padding-top: 10px;">继续支付</a>
                </p>
                <div v-if="paymentInfo.isNew || paymentInfo.showCode">
                    <img :src="paymentInfo.redirectUrl | imgFormatter" v-if="paymentInfo.redirectUrl" width="256" />
                    <p style="color: #FF9800;">支付金额：<span>￥{{ paymentInfo.realAmonut }}</span></p>
                </div>
                <p class="tips">请准确支付指定金额，支付成功后客服需要审核 1-10 分钟，请耐心等待 </p>
            </div>
            <div class="btn_group" v-if="(!paymentInfo.isNew && !paymentInfo.showCode) || payStatusDialogShowBtn">
                <el-button type="success" @click="dialogPaySubmit">我已经支付成功</el-button>
                <el-button :loading="revokeLoading" @click="revokePayment">撤销订单</el-button>
            </div>
        </el-dialog>

        <el-dialog
            class="redirect_pay_dialog"
            title="订单已提交"
            :visible.sync="paymentInfo.redirectDialogVisible"
            width="30%"
        >
            <div class="text">
                <p style="font-size: 16px; padding: 0 0 15px;" v-if="paymentInfo.trade_no">订单号：{{ paymentInfo.trade_no }}</p>
            </div>
            <div class="btn_group">
                <el-button type="success" @click="payFinished">我已经支付成功</el-button>
                <!-- <el-button :loading="revokeLoading" @click="revokePayment">撤销订单</el-button> -->
                <el-button :loading="revokeLoading" @click="paymentInfo.redirectDialogVisible = false">取消</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import openWindow from '@/utils/openWindow'
import { charge, payment, checkPayment, revokePayment, updatePayment, paymentActivity } from '@/api/user'
import { getPaychannel } from '@/api/common'
import { phoneValidator } from '@/utils/validator'


export default {
    name: 'User-Charge',
    props: ['type', 'user'],
    components: {
        
    },

    data() {
        const test = this.$route.query.test == 'sybet';
        const minValidate = (rule, value, callback) => {
            if(test) {
                callback();
                return ;
            }

            const min = Number(this.payChannel.min || 0);
            const max = Number(this.payChannel.max || 0);

            if(value < min) {
                callback(new Error(`${this.payChannel.name} 最低充值 ${min} 元`))
                return ;
            }

            if(max && value > max) {
                callback(new Error(`${this.payChannel.name} 最高 ${max} 元`))
                return ;
            }

            callback();
        }

        return {
            formData: {
                amount: '',
                name: '',
                acount: '',
                code: '',
                activityId: '',
                phone: '',
            },

            payChannel: '',
            labelPosition: 'left',
            labelWidth: '130px',
            activityList: [],

            formRules: {
                name: [
                    { required: true, message: '请输入姓名', trigger: 'blur' },
                ],
                acount: [
                    { required: true, message: '请输入账号', trigger: 'blur' },
                ],
                amount: [
                    { required: true, message: '请输入金额', trigger: 'blur' },
                    { type: 'number', message: '金额必须为数字', trigger: 'blur' },
                    { required: true, validator: minValidate, trigger: 'blur' },
                    // { type: 'number', min: test ? 1 : 10, max: 10000 * 1000, message: '最少充值10元', trigger: 'blur' }
                ],
                code: [
                    { required: true, message: '请输入后五位数字', trigger: 'blur' },
                    { validator: (rule, value, callback) => {
                        if (!/^\d{5}$/.test(value)) {
                            callback(new Error('请输入后五位数字'));
                        }else {
                            callback();
                        }
                    }, trigger: 'blur' },
                ],

                phone: phoneValidator,
            },

            loading: false,
            dataLoading: true,
            payChannelList: {},
            cardInfo: {},
            emptyList: false,

            revokeLoading: false,
            paymentInfo: {
                realAmonut: 0,
                isNew: true,
                showCode: true,
                pay_id: '',
                order_id: '',
                trade_no: '',
                redirectUrl: '',
                payStatusDialogVisible: false,
                redirectDialogVisible: false,
            },
            
            payStatusDialogShowBtn: false,
        }
    },

    computed: {
        payChannelShowList() {
            let list = [];
            for(let key in this.payChannelList) {
                list = list.concat(this.payChannelList[key]);
            }
            return list;
        },

        amoutList() {
            const max = this.payChannel ? this.payChannel.max : 9999999999;
            const min = this.payChannel ? this.payChannel.min : 0;
            const list = [20, 50, 100, 500, 1000, 2000, 5000];
            const newList = [];

            list.forEach(n => {
                if(n <= max && n >= min) {
                    newList.push(n);
                }
            });

            return newList;
        },

        historyName() {
            let historyName = window.localStorage.getItem('historyName');

            if(historyName) {
                return JSON.parse(historyName);
            }

            return [];
        },
    },

    watch: {
        paymentInfo(val) {
            if(val.payStatusDialogVisible) {
                setTimeout(() => {
                    this.payStatusDialogShowBtn = true;
                }, 5000);
            }else {
                this.payStatusDialogShowBtn = false;
            }
        }
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.dataLoading = true;

            getPaychannel({
                agent_id: this.user.userInfo.agent_id,
            }).then(json => {
                this.dataLoading = false;
				if (json.channel.code == 200) {

                    let list = json.channel.list;

                    if(list.length == 0) {
                        this.emptyList = true;
                    }

                    this.payChannelList = list;

                    for(let key in list) {
                        if(!this.payChannel) {
                            if(list[key].length) {
                                this.payChannelChange(list[key][0]);
                            }
                        }
                    }
                }
                
                if (json.card.code == 200) {
                    this.cardInfo = json.card.list;
				}
            });
            
            this.checkPayment();
        },

        checkPayment() {
            paymentActivity().then(json => {
                if(json.code == 200) {
                    this.activityList = json.list.items;

                    this.activityList.forEach(item => {
                        if(item.type == 'SC') {
                            this.formData.activityId = item.id;
                        }
                    });
                }

                return json;
            }).then(() => {
                // checkPayment({
                //     pay_channel: 2
                // }).then(json => {
                //     if(json.code == 200) {
                //         this.paymentInfo = {
                //             redirectUrl: json.list.qrurl,
                //             order_id: json.list.id,
                //             trade_no: json.list.trade_no,
                //             realAmonut: json.list.amount,
                //             isNew: false,
                //             showCode: false,
                //             payStatusDialogVisible: true,
                //         }
                //     }
                // })
            });
        },

        getChannelImg(channel) {
            const imgs = {
                // 银行卡
                '1': '/images/2019/08/15/fe71b37f-7599-4d8b-9955-6d9048900549.png',
                // 支付宝
                '2': '/images/2019/08/15/7289c4aa-3a0a-4ee2-955b-d4609084147b.png',
                // 微信
                '3': '/images/2019/08/15/f18aee17-a925-42e6-8091-292452e69525.png',
                // 银联
                '4': '/images/2019/08/15/b493c931-8d21-4825-9c69-b70216b02d92.png',
            };

            return imgs[channel.pay_type] || '';
        },

        payChannelChange(channel) {
            this.payChannel = channel;
        },

        amountClick(amount) {
            this.formData.amount = amount;
            this.$refs['mainForm'].validateField('amount');
        },

        submitForm() {
            if(this.loading) return ;

            const sucessCb = () => {
                // console.log(this.$refs);
                this.$refs['mainForm'].validate(valid => {
                    if (valid) {

                        const data = {
                            pay_channel: this.payChannel.pay_type,
                            pay_id: this.payChannel.id,
                            pay_key: this.payChannel.key,
                            amount: this.formData.amount,
                        }

                        const activityId = this.formData.activityId;

                        if(this.activityList.length && activityId && this.payChannel.max >= 200) {
                            this.activityList.forEach(item => {
                                if(item.id == activityId) {
                                    data.activity_id = activityId;
                                }
                            })
                        }

                        const test = this.$route.query.test;

                        if(test == 'sybet') {
                            data.test = test;
                        }else {
                            const min = Number(this.payChannel.min || 0);
                            const max = Number(this.payChannel.max || 0);
                            if(data.amount < min) {
                                this.$message.error(`最低充值 ${min} 元`)
                                return ;
                            }

                            if(max && data.amount > max) {
                                this.$message.error(`最高 ${max} 元`)
                                return ;
                            }
                        }

                        if(data.pay_id == 5) {
                            data.in_account = this.cardInfo.bank_account;
                            data.in_name = this.cardInfo.name;
                            data.in_branch_name = this.cardInfo.open_account_bank;
                            data.in_subbranch_name = this.cardInfo.open_account_address;
                            data.name = this.formData.name;
                        } else if(data.pay_id == 3) {
                            data.name = this.formData.name;
                            data.code = this.formData.code;
                        } else if(data.pay_id == 4) {
                            data.name = this.formData.name;
                            data.code = this.formData.code;
                            // data.acount = this.formData.acount;
                        } else if(data.pay_id == 7) {
                            data.acount = this.formData.phone;
                        }

                        // 存入历史记录
                        if(data.name) {
                            this.saveHistoryName(data.name);
                        }
                        
                        const urlType = this.payChannel.urlType;

                        // console.log(data);return ;

                        this.loading = true;
                        payment(data).then(json => {
                            this.loading = false;
                            if (json.code == 200) {
                                if(urlType == 'qrcode') {
                                    this.paymentInfo = {
                                        redirectUrl: json.list.qrurl,
                                        order_id: json.list.order_id,
                                        trade_no: json.list.trade_no,
                                        realAmonut: data.amount,
                                        isNew: true,
                                        showCode: true,
                                        payStatusDialogVisible: true
                                    }
                                }else if(urlType == 'redirect') {
                                    if(json.list.qrurl) {
                                        this.paymentInfo.pay_id = json.list.pay_id;
                                        this.paymentInfo.order_id = json.list.order_id;
                                        this.paymentInfo.trade_no = json.list.trade_no;
                                        this.paymentInfo.redirectDialogVisible = true;
                                        openWindow(json.list.qrurl);
                                    }else {
                                        this.$message.error('二维码为空');
                                    }
                                }else if(urlType == 'check') {
                                    this.$message.success('订单提交成功，请等待客服审核');
                                    this.$router.push({ path: '/user/transfer' })
                                }
                            } else {
                                this.$message.error(json.info);
                            }
                        });
                    }
                });
            }

            let validFormName = [];

            if(this.payChannel.id == 5) {
                validFormName.push('nameForm');
            }else if(this.payChannel.id == 4) {
                validFormName.push('nameForm');
                validFormName.push('codeForm');
                // validFormName.push('acountForm');
            }else if(this.payChannel.id == 3) {
                validFormName.push('nameForm');
                validFormName.push('codeForm');
            }

            if(validFormName.length) {
                let checkValid = true;

                validFormName.forEach(formName => {
                    this.$refs[formName].validate(valid => {
                        if (!valid) {
                            checkValid = false;
                        }
                    });
                })
    
                if(checkValid) {
                    sucessCb();
                }
            }else {
                sucessCb();
            }
        },

        dialogPaySubmit() {
            this.$message.success('提交成功，请等待客服审核');
            this.$router.push({ path: '/user/transfer' });
        },
        
        payFinished() {
            checkPayment({
                pay_id: this.paymentInfo.pay_id,
                pay_channel: this.payChannel.id
            }).then(json => {
                this.paymentInfo.redirectDialogVisible = false;
                this.$router.push({ path: '/user/transfer' });
            });
        },

        revokePayment() {
            this.$confirm('确认撤销此支付订单？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                const data = {
                    order_id: this.paymentInfo.order_id
                }

                if(this.revokeLoading) return ;
                this.revokeLoading = true;
                revokePayment(data).then(json => {
                    this.revokeLoading = false;
                    if(json.code == 200) {
                        this.$message.success('订单撤销成功');
                        this.paymentInfo.payStatusDialogVisible = false
                    }else {
                        this.$message.error(json.info);
                    }
                })
            }).catch(() => {
                
            });
        },

        saveHistoryName(name) {
            let historyName = window.localStorage.getItem('historyName');

            if(historyName) {
                historyName = JSON.parse(historyName);
            }else {
                historyName = [];
            }

            if(historyName.indexOf(name) == -1) {
                historyName.push(name)
                window.localStorage.setItem('historyName', JSON.stringify(historyName));
            }
        },

        checkIsShowProp(prop) {
            const payChannel = this.payChannel;

            if(!payChannel) return false;

            const pid = payChannel.id;
            if(prop == 'qrcode') {
                return (pid == 3 || pid == 4 || pid == 7) && payChannel.qrcodeImg;
            } else if(prop == 'name') {
                return pid == 3 || pid == 4 || pid == 5;
            } else if(prop == 'code') {
                return pid == 3 || pid == 4;
            } else if(prop == 'phone') {
                return pid == 7;
            }
        }
    }
}
</script>

<style lang="scss">

