            border-color: #333;
            box-shadow: none;
            color: #fff;
            position: relative;
            // padding: 7px 12px;
            border-radius: 4px;
            border-color: #525965;
            background: #525965;
            min-width: 60px;

            div {
                margin-top: 5px;
            }

            .right {
                display: none;
                background-image: url(~@/assets/right-icon.png);
                background-repeat: no-repeat;
                background-size: contain;
                width: 15px;
                height: 15px;
                position: absolute;
                bottom: -1px;
                right: -1px;
            }

            &.active {
                border-color: #00aeff!important;
                color: #00aeff;

                .right {
                    display: block;
                }
            }
        }

        .form_amount {
            .tip {
                font-size: 12px;
                color: #dedede;
                padding-left: 10px;
            }

            .sBtn {
                border-color: #333;
                background: #424242;
            }
        }

		.pay_detail {
            width: 350px;
            padding: 20px;
            // border: 1px solid #8a8a8a;
            background: #424242;
            border: none;
            border-radius: 4px;
		}
    }

    .qrcode_tip {
        padding-top: 15px;
