<template>
    <div class="user_info">
        <el-tabs :stretch="true" v-model="activeName" tab-position="top">
            <el-tab-pane label="基本信息" name="info">
                <el-form ref="infoForm" :model="infoForm" :rules="infoFormRules" label-position="left" label-width="130px">
                    <el-form-item label="用户名">
                        {{ user.userInfo.username }}
                    </el-form-item>
                    <el-form-item label="手机号">
                        <router-link v-if="!user.userInfo.mobile_phone" to="/user/info?t=phone">绑定手机号</router-link>
                        <span v-else>{{ user.userInfo.mobile_phone }}</span>
                    </el-form-item>
                    <el-form-item label="邮箱">
                        <router-link v-if="!user.userInfo.email" to="/user/info?t=email">邮箱</router-link>
                        <span v-else>{{ user.userInfo.email }}</span>
                    </el-form-item>
                    <!-- <el-form-item prop="name" label="姓名">
                        <el-input type="text" size="small" v-model="infoForm.name" placeholder="请输入姓名"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="small" :loading="loading" @click="submitForm('infoForm')" type="primary">提交</el-button>
                    </el-form-item> -->
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="提款密码管理" name="secure">
                <el-form ref="drawPasswdForm" :model="drawPasswdForm" :rules="drawPasswdFormRules" label-position="left" label-width="130px">
                    <el-form-item prop="oldSecurityCode" label="旧提款密码" v-if="user.userInfo.has_draw_passwd">
                    <el-input size="small" type="password" v-model="drawPasswdForm.oldSecurityCode" placeholder="请输入旧6位纯数字提款密码"></el-input>
                    </el-form-item>
                    <el-form-item prop="securityCode" v-bind:label="(!user.userInfo.has_draw_passwd?'':'新') + '提款密码'">
                        <el-input size="small" type="password" v-model="drawPasswdForm.securityCode" v-bind:placeholder="'请输入' + (!user.userInfo.has_draw_passwd? '' : '新') + '6位纯数字提款密码'"></el-input>
                    </el-form-item>
                    <el-form-item prop="reSecurityCode"  v-bind:label="'确认' + (!user.userInfo.has_draw_passwd?'':'新') + '提款密码'">
                        <el-input size="small" type="password" v-model="drawPasswdForm.reSecurityCode" v-bind:placeholder="'请再次输入' + (!user.userInfo.has_draw_passwd? '' : '新') + '6位纯数字提款密码'"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="small" :loading="loading" @click="submitForm('drawPasswdForm')" type="primary">提交</el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="银行卡管理" name="bank">
                <div v-if="!user.userInfo.has_draw_passwd">
                    <p class="form-control-plaintext">
                    为了资金安全，请先
                    <router-link :to="{ path: '/user/info?t=secure' }" class="ml-1">
                        设置提款密码
                        </router-link>
                    </p>
                </div>
                <div v-else>
                    <el-form ref="bankForm" v-if="!user.bankcardList.length" :model="bankForm" :rules="bankFormRules" label-width="130px" label-position="left">
                        <el-form-item label="开户银行" prop="open_account_bank">
                            <el-select v-model="bankForm.open_account_bank" placeholder="请选择">
                                <el-option
                                v-for="name in bank_list"
                                :key="name"
                                :label="name"
                                :value="name">
                                </el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item label="开户城市" prop="areaSelect">
                            <el-cascader
                                change-on-select
                                expand-trigger="hover"
                                :options="areaOptions"
                                v-model="bankForm.areaSelect"
                            >
                            </el-cascader>
                        </el-form-item>
                        <el-form-item label="银行卡号" prop="bank_account">
                            <el-input v-model="bankForm.bank_account" placeholder="请输入银行卡号"></el-input>
                        </el-form-item>

                        <el-form-item label="开户姓名" prop="name">
                            <el-input v-model="bankForm.name" placeholder="请输入姓名"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button size="small" :loading="loading" @click="submitForm('bankForm')" type="primary">提交</el-button>
                        </el-form-item>
                    </el-form>
                    <div v-else>
                        <div style="color: #cc5151; font-size: 12px; margin-bottom: 10px;">*如需修改或删除银行卡请联系客服</div>

                        <el-form :key="card.id" v-for="card in user.bankcardList" label-width="130px" label-position="left">
                            <el-form-item label="开户银行" prop="open_account_bank">
                                {{ card.open_account_bank }} 
                            </el-form-item>

                            <el-form-item label="开户城市" prop="areaSelect">
                                {{ card.open_account_province }}
                            </el-form-item>
                            <el-form-item label="银行卡号" prop="bank_account">
                                {{ card.bank_account }}
                            </el-form-item>

                            <el-form-item label="开户姓名" prop="name">
                                {{ card.name }}
                            </el-form-item>
                        </el-form>
                    </div>
                </div>

            </el-tab-pane>
            <el-tab-pane label="密码管理" name="password">
                <el-form ref="pwdForm" :model="pwdForm" :rules="pwdFormRules" label-width="130px" label-position="left">
                    <el-form-item prop="oldPassword" label="旧密码">
                        <el-input type="password" v-model="pwdForm.oldPassword" placeholder="请输入旧密码"></el-input>
                    </el-form-item>
                    <el-form-item prop="password" label="新密码">
                        <el-input type="password" v-model="pwdForm.password" placeholder="请输入新密码"></el-input>
                    </el-form-item>
                    <el-form-item prop="rePassword" label="确认新密码">
                        <el-input type="password" v-model="pwdForm.rePassword" placeholder="请再次输入新密码"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="small" :loading="loading" @click="submitForm('pwdForm')" type="primary">提交</el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="手机号管理" name="phone">
                <el-form ref="mobileForm" :model="mobileForm" :rules="mobileFormRules" label-width="130px" label-position="left">
                    <el-form-item v-if="user.userInfo.mobile_phone" prop="mobile_phone" label="绑定手机号">
                        {{ user.userInfo.mobile_phone }}
                    </el-form-item>
                    <el-form-item prop="phone" :label="'输入'+ (!hasPhone ? '' : '新') +'手机号'">
                        <el-input v-model="mobileForm.phone" v-bind:placeholder="'请输入' + (!hasPhone ? '' : '新') + '手机号'"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="small" :loading="loading" @click="submitForm('mobileForm')" type="primary">提交</el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="邮箱管理" name="email">
                <el-form ref="emailForm" :model="emailForm" :rules="emailFormRules" label-width="130px" label-position="left">
                    <el-form-item v-if="user.userInfo.email" prop="email" label="绑定邮箱">
                        {{ user.userInfo.email }}
                    </el-form-item>
                    <el-form-item prop="email" :label="'输入'+ (!hasEmail ? '' : '新') +'邮箱'">
                        <el-input v-model="emailForm.email" v-bind:placeholder="'请输入' + (!hasEmail ? '' : '新') + '邮箱'"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button size="small" :loading="loading" @click="submitForm('emailForm')" type="primary">提交</el-button>
                    </el-form-item>
                </el-form>

            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { imgFormatter } from '@/utils/index'
import { getAddr } from '@/api/common'
import { 
    bankAccountValidator,
    bankNameValidator,
} from '@/utils/validator'
import { 
    passwordValidator, 
    drawPasswordValidator, 
    phoneValidator, 
    emailValidator,
    phoneCodeValidator,
    emailCodeValidator,
} from '@/utils/validator'
import { modify } from '@/api/user'




export default {
    name: 'User-Info',
    props: ['type', 'user', 'product'],
    components: {
    },

    data() {
        var validateArea = (rule, value, callback) => {
            if (!value || value.length != 2) {
                callback(new Error('请选择开户城市'));
            } else {
                callback();
            }
        };

        return {
            loading: false,

            activeName: 'info',

            infoForm: {
                name: '',
                type: 'info',
            },

            infoFormRules: {

            },

            // 安全码
            drawPasswdForm: {
                oldSecurityCode: '',
                securityCode: '',
                reSecurityCode: '',
                type: 'security_code',
            },

            drawPasswdFormRules: {
                oldSecurityCode: drawPasswordValidator,
                securityCode: drawPasswordValidator,
                reSecurityCode: drawPasswordValidator,
            },

            // 银行卡
            bank_list: [
                '工商银行', '建设银行', '农业银行', '民生银行', '招商银行', '交通银行', '中国银行', '邮政储蓄', '中信银行', '兴业银行', '华夏银行', '浦发银行', '广发银行', '平安银行', '光大银行', '农商银行', '台州银行'
            ],
            
            areaOptions: [],
            
            bankForm: {
                id: '',
                open_account_bank: '',
                areaSelect: [],
                bank_account: '',
                name: '',
            },
            
            bankFormRules: {
                open_account_bank: [{ required: true, message: '请选择开户银行', trigger: 'change' }],
                areaSelect: [{ required: true, validator: validateArea, trigger: 'change' }],
                bank_account: [{ required: true, message: '请选择银行卡号', trigger: 'blur' }],
                bank_account: bankAccountValidator,
                name: bankNameValidator,
            },

            // 密码
            pwdForm: {
                oldPassword: '',
                password: '',
                rePassword: '',
                type: 'password',
            },

            pwdFormRules: {
                oldPassword: passwordValidator,
                password: passwordValidator,
                rePassword: passwordValidator,
            },

            // 手机号
            mobileForm: {
                phone: '',
                type: 'phone',
            },

            mobileFormRules: {
                phone: phoneValidator,
            },

            // 邮箱
            emailForm: {
                email: '',
                type: 'email',
            },

            emailFormRules: {
                email: emailValidator,
            },
        }
    },

    computed: {
        
        hasPhone() {
            return this.user.userInfo.mobile_phone;
        },
        hasEmail() {
            return this.user.userInfo.email;
        },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            if(this.areaOptions.length == 0) {
                getAddr().then(json => {
                    if (json.code == 200) {
                        const areaOptions = json.list;
                        
                        areaOptions.forEach(province => {
                            province.value = province.label = province.name;
                            province.children = province.cities;

                            province.children.forEach(city => {
                                city.value = city.label = city.name;
                            });
                        });
                        
                        this.areaOptions = areaOptions;
                    }
                })
            }

            if(!this.user.bankcardList.length) {
                this.$store.dispatch('bankcardList')
            }

            const t = this.$route.query.t;
            this.activeName = t || 'info';
        },

        submitForm(formName) {
            const r = decodeURIComponent(this.$route.query.r || '');
            const data = {};

            let form = null, formData = null;

            this.$refs[formName].validate((valid) => {
                if (valid) {
                    switch(formName) {
                        case 'drawPasswdForm':
                            formData = this.drawPasswdForm;

                            if (formData.securityCode != formData.reSecurityCode) {
                                this.$message.error('提款码不一致');
                                return;
                            }

                            if(this.user.userInfo.has_draw_passwd) {

                                if(formData.oldSecurityCode == formData.securityCode) {
                                    this.$message.error('不能与原提款码一致');
                                    return;
                                }

                                data.old_draw_passwd = formData.oldSecurityCode;
                                data.draw_passwd = formData.securityCode;
                            }else {
                                data.key = 'draw_passwd';
                                data.val = formData.securityCode;
                            }
                        break;
                        case 'bankForm':
                            this.submitBank();
                        break;
                        case 'pwdForm':
                            formData = this.pwdForm;
                            if (formData.password != formData.rePassword) {
                                this.$message.error('密码不一致');
                                return ;
                            }

                            if(formData.oldPassword == formData.password) {
                                this.$message.error('不能与原密码一致');
                                return;
                            }

                            data.old_password = formData.oldPassword;
				            data.password = formData.password;
                        break;
                        case 'mobileForm':
                            formData = this.mobileForm;
                            data.key = 'mobile_phone';
                            data.val = formData.phone;
                        break;
                        case 'emailForm':
                            formData = this.emailForm;
                            data.key = 'email';
                            data.val = formData.email;
                        break;
                    }

                    data.type = formData.type;

                    // console.log(data);return ;

                    if(this.loading) return ;
                    this.loading = true;
                    modify(data).then(json => {
                        this.loading = false;
                        if (json.code == 200) {
                            this.$message.success('提交成功');
                            this.$store.dispatch('getUserInfo');
                            // this.formData = Object.assign({}, defaultFormData);
                            this.$router.push({ path: r || '/user/info?t=info' });
                        } else {
                            this.$message.error(json.info);
                        }
                    });
                }
            });
        },

        submitBank() {
            const formData = this.bankForm;

            const data = {
                id: formData.id,
                name: formData.name,
                open_account_bank: formData.open_account_bank,
                bank_account: formData.bank_account,
                open_account_province: formData.areaSelect[0] + '-' + formData.areaSelect[1],
                open_account_address: '',
                id_card: '',
            }

            if(this.loading) return ;
            this.loading = true;
            this.$store.dispatch('addBankcard', data).then(json => {
                this.loading = false;

                if (json.code == 200) {
                    this.$message.success('绑定成功');
                    // this.form = {
                    //     id: '',
                    //     open_account_bank: '',
                    //     areaSelect: [],
                    //     bank_account: '',
                    //     name: '',
                    // };
                    
                    this.$store.dispatch('bankcardList');

                } else {
                    this.$message.error(json.info);
                }
            })
        }
    }
}
</script>

