<template>
    <div class="user_order">

        <div class="filter">
            <!-- <label>类型：</label>
            <el-select v-model="formData.type" placeholder="选择类型" class="type_select">
                <el-option
                    v-for="item in statusOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select> &nbsp; -->
            <label>游戏类型：</label>
            <el-select size="small" clearable v-model="formData.product_id" placeholder="选择游戏" class="type_select">
                <el-option
                    v-for="item in productListFilter"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
            </el-select> &nbsp;
            <label>日期：</label>
            <el-date-picker
                size="small"
                v-model="formData.date"
                type="daterange"
                align="right"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="datePickerOptions"
                class="date_picker"
            >
            </el-date-picker>
            &nbsp;<el-button size="small" type="primary" @click="filterSubmit">筛选</el-button>
            <el-button size="small" type="primary" plain @click="filterSubmit">刷新</el-button>
        </div>

        <div v-if="totalData" class="total_data">
            总计：投注额：￥{{ totalData.cathectic_total_price }}，有效投注额：￥{{ totalData.vaild_cathectic_total_price }}，派彩：￥{{ totalData.winning_total_price }}，笔数：{{ totalData.total_order }}
        </div>
        <div>
            <el-table
            ref="table"
            size="small"
            :data="list.items"
            v-loading="loading"
            :element-loading-text="'加载中'"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
            style="width: 100%">
                <el-table-column type="expand" width="1px">
                    <template slot-scope="scope">
                        <GameDetail :data="scope.row" :detail="scope.row.detail" :statusText="statusOptions[scope.row.status]" />
                    </template>
                </el-table-column>
                <el-table-column
                    prop="order_sn"
                    width="210"
                    label="订单号"
                ></el-table-column>
                <el-table-column
                    label="游戏名称"
                    width="180"
                >
                    <template slot-scope="scope">
                        {{ scope.row.product_name + '-' + scope.row.game_name }}
                    </template>
                </el-table-column>

                <!-- <el-table-column
                    label="投注额"
                >
                    <template slot-scope="scope">
                        {{ '￥' + scope.row.cathectic_total_price }}
                    </template>
                </el-table-column> -->

                <el-table-column
                    label="有效投注额"
                >
                    <template slot-scope="scope">
                        {{ '￥' + scope.row.vaild_cathectic_total_price }}
                    </template>
                </el-table-column>

                <el-table-column
                    label="派彩"
                >
                    <template slot-scope="scope">
                        {{ scope.row.status == 3 ? '￥' + scope.row.winning_total_price : '-' }}
                    </template>
                </el-table-column>

                <el-table-column
                    label="损益"
                >
                    <template slot-scope="scope">
                        {{ scope.row.status == 3 ? '￥' + scope.row.balance_total_price : '-' }}
                    </template>
                </el-table-column>

                <el-table-column
                    label="状态"
                >
                    <template slot-scope="scope">
                        <el-tag size="small" effect="dark" :type="statusClassOptions[scope.row.status]">{{ statusOptions[scope.row.status] }}</el-tag>
                    </template>
                </el-table-column>

                <el-table-column
                    label="投注时间"
                    width="120"
                >
                    <template slot-scope="scope">
                        {{ formatTime(scope.row.begintime) }}
                    </template>
                </el-table-column>

                <el-table-column
                    label="操作"
                >
                    <template slot-scope="scope">
                          <el-button class="detail_btn" :loading="detailLoading[scope.row.id]" type="text" @click="showDetail(scope.row)">详细</el-button>
                    </template>
                </el-table-column>

            </el-table>
            <el-pagination
                background
                layout="prev, pager, next"
                @current-change="pageChange"
                :page-count="list.total_pages"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import request from '@/utils/request'
import { pickerOptions, formatTime, formatBankAcount } from '@/utils/index'
import { getList, getDetail } from '@/api/order'
import GameDetail from './GameDetail'

export default {
    name: 'User-Order',
    props: ['type', 'user', 'product'],
    components: {
        GameDetail,
    },

    data() {
        return {
            list: {},
            totalData: null,
            datePickerOptions: {
                shortcuts: pickerOptions
            },
            loading: false,
            detailLoading: {},
            formData: {
                date: '',
                type: '',
                product_id: '', 
                page: 1,
            },
            statusOptions: {
                1: '已创建',
                2: '已支付',
                3: '已完成',
                4: '取消',
            },
            statusClassOptions: {
                '1': 'warning',
                '2': 'info',
                '3': 'success',
                '4': 'info',
            },
        }
    },

    computed: {
        productListFilter() {
            let list = [];

            this.product.navList.forEach(item => {
                if(item.is_show == 'Y' && item.list && item.list.length) {
                    list = list.concat( item.list.filter(l => l.is_show == 'Y') );
                }
            })

            return list;
        },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.requestList();
        },

        requestList() {
            if(this.loading) return ;
            const data = {
                type: this.formData.type,
                product_id: this.formData.product_id,
                page: this.formData.page,
            }

            if(this.formData.date && this.formData.date.length == 2) {
                const starttime = this.formData.date[0];
                const endtime = this.formData.date[1];

                starttime.setHours(0);
                starttime.setMinutes(0);
                starttime.setSeconds(0);
                starttime.setMilliseconds(0);

                endtime.setHours(23);
                endtime.setMinutes(59);
                endtime.setSeconds(59);
                endtime.setMilliseconds(999);

                data.starttime = parseInt((+starttime)/1000);
                data.endtime = parseInt((+endtime)/1000);
            }

            this.loading = true;
            getList(data).then(json => {
                this.loading = false;
                if (json.code == 200) {
                    this.list = json.list;
                    if(json.list.totalData) {
                        this.totalData = json.list.totalData;
                    }
                    
				}else if(json.code == 10002) {
                    this.list = {};
                }
            });
        },

        formatTime(t) {
            return formatTime(t);
        },

        getStatusText(s) {
            return this.statusOptions.filter(t => { return t.value == s })[0].label;
        },

        showDetail(row) {
            if(row.detail) {
                this.$refs.table.toggleRowExpansion(row);
                return ;
            }

            if(this.detailLoading[row.id]) return ;

            this.detailLoading[row.id] = true;
            getDetail(row.order_sn).then(json => {
                this.detailLoading[row.id] = false;
                if (json.code == 200) {
                    row.detail = json.list;
                    this.$refs.table.toggleRowExpansion(row);
                }
            });
        },

        isAmountInput(row) {
            if(row.type == 1) {
                return true;
            }else if(row.type == 2) {
                return false;
            }else if(row.type == 3) {
                return true;
            }else if(row.type == 4) {
                return false;
            }

            return true;
        },

        getTradeAt(row) {
            return formatTime(row.trade_at == '0' ? row.created_at : row.trade_at);
        },

        getAcoutCodeText(row) {
            if(row.type == 1) {
                return row.acount.type_text + '充值';
            }else if(row.type == 2) {
                return '提现至银行卡号：' + formatBankAcount(row.acount.acount);
            }else if(row.type == 3) {
                return row.product.name + ' 转至 余额';
            }else if(row.type == 4) {
                return  '余额 转至 ' + row.product.name;
            }

            return '';
        },

        filterSubmit() {
            this.requestList();
        },

        pageChange(currentPage) {
            this.formData.page = currentPage;
            this.requestList();
        }

    }
}
</script>

