<script>
import { mapGetters } from 'vuex'
import MoneyDropdown from '@/views/layout/components/MoneyDropdown'
import { imgFormatter } from '@/utils/index'
import { moneyToFixed } from '@/filters/index'

export default {
    name: 'User-Info',
    props: ['type', 'user', 'product'],
    components: {
        MoneyDropdown
    },

    data() {
        return {
            vipInfo: null,
        }
    },

    computed: {
		app() { return this.$store.getters.app },
    },

    mounted() {
        this.$store.dispatch('getConfig', 'vip').then(list => {
            this.vipInfo = list;
        });
    },

    methods: {

        getPercentage(a, b) {
            if(b == 0) {
                return 100;
            }

            const p = Number(a)/Number(b);

            if(p > 1) {
                return 100;
            }

            return moneyToFixed(p * 100);
        },
        
    }
}
</script>

